
import Toast from 'vux/src/components/toast/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'



export default {
name: '',
directives: {
TransferDom
},
components: {
Toast,
XButton,
Tab,
TabItem,
Grid,
GridItem,
Group,
Cell,
Confirm
},
data () {
return {
confirmShow: false,
avatar: require('../../assets/images/my/avatar.png'),
toastData: {
isShow: false,
type: 'warn',
text: ''
},
json: {
order: 'ASC',
pageNum: 1,
pageSize: 100,
status: 0,
typeid: 1
},
orderUrl: 'order/orderlist',
tabIndex: 0,
tabs: [
{
title: '',
type: 1
},
{
title: '',
type: 2
},
{
title: '',
type: 3
}
],
tabs1: [
{
id: 1,
img: require('../../assets/images/my/vip1-1.png'),
title: '',
nums: 0
},
{
id: 2,
img: require('../../assets/images/my/vip1-2.png'),
title: '',
nums: 0
},
{
id: 3,
img: require('../../assets/images/my/vip1-3.png'),
title: '',
nums: 0
},
{
id: 4,
img: require('../../assets/images/my/vip1-4.png'),
title: '',
nums: 0
}
],
orderLens: [],
mylists: [ // type: 0()1()2()3() , id: 0()
{
icon: require('../../assets/images/my/vip2-1.png'),
title: '',
url: 'order',
type: 1,
id: 0
},
{
icon: require('../../assets/images/my/vip2-2.png'),
title: '',
url: 'order',
type: 3,
id: 0
},
{
icon: require('../../assets/images/my/vip2-3.png'),
title: '',
url: 'cart'
},
{
icon: require('../../assets/images/my/vip2-4.png'),
title: '',
url: 'points'
},
{
icon: require('../../assets/images/my/vip2-5.png'),
title: '',
url: 'detail'
},
{
icon: require('../../assets/images/my/vip2-6.png'),
title: '',
url: 'address',
type: 0
},
{
icon: require('../../assets/images/my/vip2-7.png'),
title: '',
url: 'changePassword'
}
],
detail: {}
}
},
async created () {
let result = await this.axios.post('member/detail')
if (result.success) {
this.detail = result.data
localStorage.setItem('integral', result.data.integral)
}
this.getOrderLens()
},
methods: {
tab (index) {
console.log(index)
this.tabIndex = index
if (index === 0) { // 
this.orderUrl = 'order/orderlist'
this.json.typeid = 1
} else if (index === 1) { // 
this.orderUrl = 'order/orderintegrallist'
this.json.typeid = 1
} else if (index === 2) { // 
this.orderUrl = 'order/orderlist'
this.json.typeid = 3
}
this.getOrderLens()
},
async getOrderLens () {



console.log(this.orderUrl)
let result = await this.axios.post(this.orderUrl, this.json)
if (result.success) {
let lists = result.data.list 
console.log(lists)
this.tabs1.map((item, index) => {
item.nums = 0
})
lists.map((item, index) => {
switch (item.status) {
case 1:
this.tabs1[0].nums++
break
case 2:
this.tabs1[1].nums++
break
case 4:
this.tabs1[2].nums++
break
case 5:
this.tabs1[3].nums++
break
default: break
}
})
console.log(this.tabs1)
/* this.lists.map((item, index) => {
switch (item.status){
case 1:
break
}
}) */
/* console.log('1:', list1, list2, list3)
console.log('2:', list1, list2, list3)
console.log(this.orderLens) */
}
},
logout () {
this.confirmShow = true
},
onConfirm () {
this.axios.get('member/logout').then((result) => {
if (result.code === 0) {

this.$store.dispatch('clearToken')
this.$router.push({name: 'index'})
}
})
}
}
}



